import React from 'react';
import styled from 'styled-components'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import './Projects.css'
import {TimeAndWeather} from '@styled-icons/fluentui-system-filled/TimeAndWeather'
import { Link } from 'react-router-dom';
import weatherapp from './projectsassets/weatherapp.png'


const TimeAndWeatherIcon = styled(TimeAndWeather)`
width: 24px;
height: 24px;
padding: 5px 5px;`

const Button = styled.button`
display: flex;
font-size: 0.6em;
margin: 0 5px;
border-radius: 7px;
align-items: center;
background: lightgrey;
animation: fadeIn 1s;

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
  }
  
`



function Projects() {
    return(
    <div className="Projects">
     <h1 className="project-header">Projects</h1>
      <header className="bottom-header"> 
        <Link to="/weather" style = {{textDecoration: 'none'}} target="_blank">
        <Card sx={{ maxWidth: 500 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="300"
                image={weatherapp}
                alt="green iguana"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Daily Weather Application
                </Typography>
                <Typography variant="body2" color="text.secondary">
                An application that uses OpenWeatherMap API calls to display the daily weather of over 200,000 potential cities.
                The background theme of the application depends on the weather.
                The user can type in the city and it will display the data of the chosen city.
                </Typography>
              </CardContent>
           </CardActionArea>
         </Card>
        </Link>
      </header>
    </div>
  )
}
export default Projects;